	opacity: 0;
	visibility: hidden;
}
.raxus-slider[data-arrows=auto-hide]:hover .arrow-l, 
.raxus-slider[data-arrows=auto-hide]:hover .arrow-r {
	opacity: 1;
	visibility: visible;
}
.raxus-slider .arrow-l, 
.raxus-slider .arrow-r {
	display: inline-block;
	width: 30px;
	height: 30px;
	position: absolute;
	top: 50%;
	margin-top: -15px;
	cursor: pointer;
	z-index: 2;
	background: url(data:image/png;base64,) rgba(0,0,0,0.8) no-repeat;
	transition: all .1s;
	-moz-transition: all .1s;
	-webkit-transition: all .1s;
}

.raxus-slider .arrow-r.outer {
	right: -30px;
	border-radius: 0 2px 2px 0;
	border-left: none;
	border-right: 1px solid #000;
}
.raxus-slider .arrow-r {
	right: 0;
	border-radius: 2px 0 0 2px;
	border-right: none;
	background-position: 10px -26px;
}
.raxus-slider[data-direction=vertical] .arrow-r {
	bottom: 0;
	top: auto;
	right: auto;
	left: 50%;
	margin-left: -15px;
	border-radius: 2px 2px 0 0;
	border-bottom: none;
	background-position: -71px -26px;
}
.raxus-slider .arrow-r:active {
	background-position: 13px -26px;
}
.raxus-slider[data-direction=vertical] .arrow-r:active {
	background-position: -71px -23px;
}
.raxus-slider .arrow-l.outer {
	left: -30px;
	border-radius: 3px 0 0 3px;
	border-right: none;
	border-left: 1px solid #000;
}
.raxus-slider .arrow-l {
	left: 0;
	border-radius: 0 3px 3px 0;
	border-left: none;
	background-position: -17px -26px;
}
.raxus-slider[data-direction=vertical] .arrow-l {
	top: 0;
	margin-top: auto;
	right: auto;
	left: 50%;
	margin-left: -15px;
	border-radius: 0 0 2px 2px;
	border-top: none;
	background-position: -42px -27px;
}
.raxus-slider .arrow-l:active {
	background-position: -20px -26px;
}
.raxus-slider[data-direction=vertical] .arrow-l:active {
	background-position: -42px -30px;
}
.raxus-slider .slider-area {
	overflow: hidden;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	background-color: #444;
