}
.raxus-slider[data-thumbnail=left] .slider-area {
	border-radius: 0 4px 4px 0;
}

.raxus-slider .slider-relative {
	width: 100%;
	position: absolute;
	font-size: 0;
	left: 0;
	margin: 0;
	padding: 0;
	height: 100%;
	text-align: left;
	white-space: nowrap;
	transition: all .5s;
	-webkit-transition: all .5s;
	-moz-transition: all .5s;
	-o-transition: all .5s;
}
.raxus-slider .slider-relative .slide {
	padding: 0;
	height: 100%;
	width: 100%;
	display: inline-block;
	font-size: 0;
	text-align: center;
	position: relative;
	overflow: hidden;
	vertical-align: middle;
	background-color: #111;
}
.raxus-slider .slider-relative .slide img {
	opacity: 0;
	transition: all .5s;
	-webkit-transition: all .5s;
	-moz-transition: all .5s;
	-o-transition: all .5s;
}
.raxus-slider .slider-relative .slide.selected img {
	opacity: 1;
	-webkit-backface-visibility: hidden;
	-moz-backface-visibility: hidden;
	-ms-backface-visibility: hidden;
    -webkit-perspective: 1000;
}
.raxus-slider[data-direction=vertical] .slider-relative .slide {
